import React from "react";
import './../styles/layouts/main-layout.css';

import { Badge } from '@material-ui/core'

import Menu from './../views/common/menu-view';
import DashboardView from './../views/dashboard/dashboard-view';

class MainLayout extends React.Component {
    render() {
        return (
            <div className="layout-root">
                <header>
                    <div className="logo"></div>
                    <div className="better-meter"></div>
                    <div className="header-controls">
                        <div className="search">
                            <div className="search-icon"></div>
                        </div>
                        <div className="notifications">
                            <div className="notifications-icon"></div>
                            <Badge badgeContent={9}>
                            </Badge>
                        </div>
                        <div className="user">
                            <div className="avatar"></div>
                            <div className="username">Adrian Villa</div>
                            <div className="choose-user-icon"></div>
                        </div>
                    </div>
                </header>
                <div className="layout-container">
                    <Menu />
                    <DashboardView />
                </div>
                <footer>© All right reserved. BetterMeter</footer>
            </div>
        );
    }
}

export default MainLayout;