import React from "react";
import './../../styles/views/common/menu-view.css';

import { Tabs, Tab } from '@material-ui/core'

class Menu extends React.Component {
    render() {
        return (
            <Tabs value={0} className="main-menu">
                <Tab label="Dashboard" icon={<div className="dashboard-icon"></div>}></Tab>
                <Tab label="Sites"></Tab>
                <Tab label="Campaigns"></Tab>
                <Tab label="Analytics"></Tab>
                <Tab label="Live View"></Tab>
                <Tab label="Users"></Tab>
                <Tab label="Organizations"></Tab>
                <Tab label="Settings"></Tab>
            </Tabs>
        );
    }
}

export default Menu;