import React from "react";
import './../../styles/views/dashboard/dashboard-view.css';

import { Select, MenuItem, FormControl, InputLabel, Paper, Tabs, Tab, } from '@material-ui/core'
import DateFnsUtils from '@date-io/date-fns';
import { MuiPickersUtilsProvider, KeyboardDatePicker } from '@material-ui/pickers';

class DashboardView extends React.Component {
    render() {
        return (
            <div className="dashboard-container">
                <div className="inner-container">
                    <div className="dashboard-header">
                        <h1>Analytics</h1>
                        <div className="filters">
                            <FormControl variant="outlined" className="sites-container">
                                <InputLabel id="sites-select-label">Sites</InputLabel>
                                <Select labelId="sites-select-label" label="Sites">
                                    <MenuItem>DrawKit.io</MenuItem>
                                    <MenuItem>DesignStripe</MenuItem>
                                    <MenuItem>Glyphy.io</MenuItem>
                                    <MenuItem>Designvalley.club</MenuItem>
                                    <MenuItem>Neubel</MenuItem>
                                </Select>
                            </FormControl>
                            <FormControl variant="outlined" className="events-container">
                                <InputLabel id="events-select-label">Events</InputLabel>
                                <Select labelId="events-select-label" label="Events">
                                    <MenuItem>All</MenuItem>
                                </Select>
                            </FormControl>
                            <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                <KeyboardDatePicker
                                    variant="inline"
                                    value={new Date()}
                                    onChange={() => { }}
                                    format="MM/dd/yyyy">
                                </KeyboardDatePicker>
                            </MuiPickersUtilsProvider>
                        </div>
                    </div>
                    <Paper elevation={0} className="dashboard-paper">
                        <Tabs value={0} className="dashboard-tabs">
                            <Tab label={
                                <div className="tab-header">
                                    <div className="tab-title">Pageviews</div>
                                    <div className="tab-content">
                                        <div className="tab-icon"></div>
                                        <div className="tab-amount">223.5k</div>
                                        <div className="tab-change">+15%</div>
                                    </div>
                                </div>
                            }></Tab>
                            <Tab label={
                                <div className="tab-header">
                                    <div className="tab-title">Unique Users</div>
                                    <div className="tab-content">
                                        <div className="tab-icon"></div>
                                        <div className="tab-amount">74.858</div>
                                        <div className="tab-change">-15%</div>
                                    </div>
                                </div>
                            }></Tab>
                            <Tab label={
                                <div className="tab-header">
                                    <div className="tab-title">Average Visit Time</div>
                                    <div className="tab-content">
                                        <div className="tab-icon"></div>
                                        <div className="tab-amount">01:45</div>
                                        <div className="tab-change">+15%</div>
                                    </div>
                                </div>
                            }></Tab>
                            <Tab label={
                                <div className="tab-header">
                                    <div className="tab-title">Bounce Rate</div>
                                    <div className="tab-content">
                                        <div className="tab-icon"></div>
                                        <div className="tab-amount">25.7%</div>
                                        <div className="tab-change">-15%</div>
                                    </div>
                                </div>
                            }></Tab>
                        </Tabs>
                        <div className="dashboard-content">
                            <div className="dashboard-pageviews">
                                <ul>
                                    <li>
                                        <div className="pageview-icon">GL</div>
                                        <div className="pageview-amount">264,738</div>
                                    </li>
                                    <li>
                                        <div className="pageview-icon">DS</div>
                                        <div className="pageview-amount">64,738</div>
                                    </li>
                                    <li>
                                        <div className="pageview-icon">GL</div>
                                        <div className="pageview-amount">23,738</div>
                                    </li>
                                    <li>
                                        <div className="pageview-icon">DK</div>
                                        <div className="pageview-amount">8,837</div>
                                    </li>
                                    <li>
                                        <div className="pageview-icon">DS</div>
                                        <div className="pageview-amount">5,375</div>
                                    </li>
                                    <li>
                                        <div className="pageview-icon">NB</div>
                                        <div className="pageview-amount">3,923</div>
                                    </li>
                                </ul>
                            </div>
                            <div className="dashboard-graph"></div>
                        </div>
                    </Paper>
                    <div className="cards-container">
                        <Paper elevation={0} className="bottom-card">
                            <h2>Most Active Sites</h2>
                            <ul>
                                <li>
                                    <div className="bottom-card-icon">GL</div>
                                    <div className="bottom-card-name">Glyphy</div>
                                    <div className="bottom-card-amount">264,738</div>
                                </li>
                                <li>
                                    <div className="bottom-card-icon">DS</div>
                                    <div className="bottom-card-name">DesignStripe</div>
                                    <div className="bottom-card-amount">64,738</div>
                                </li>
                                <li>
                                    <div className="bottom-card-icon">DV</div>
                                    <div className="bottom-card-name">Design Valley</div>
                                    <div className="bottom-card-amount">23,738</div>
                                </li>
                                <li>
                                    <div className="bottom-card-icon">DK</div>
                                    <div className="bottom-card-name">Neubel</div>
                                    <div className="bottom-card-amount">8,837</div>
                                </li>
                                <li>
                                    <div className="bottom-card-icon">DS</div>
                                    <div className="bottom-card-name">Last Year</div>
                                    <div className="bottom-card-amount">5,375</div>
                                </li>
                            </ul>
                        </Paper>
                        <Paper elevation={0} className="bottom-card">
                            <h2>Most Visited Sites</h2>
                            <ul>
                                <li>
                                    <div className="bottom-card-icon">GL</div>
                                    <div className="bottom-card-name">Glyphy</div>
                                    <div className="bottom-card-amount">264,738</div>
                                </li>
                                <li>
                                    <div className="bottom-card-icon">DS</div>
                                    <div className="bottom-card-name">DesignStripe</div>
                                    <div className="bottom-card-amount">64,738</div>
                                </li>
                                <li>
                                    <div className="bottom-card-icon">DV</div>
                                    <div className="bottom-card-name">Design Valley</div>
                                    <div className="bottom-card-amount">23,738</div>
                                </li>
                                <li>
                                    <div className="bottom-card-icon">DK</div>
                                    <div className="bottom-card-name">Neubel</div>
                                    <div className="bottom-card-amount">8,837</div>
                                </li>
                                <li>
                                    <div className="bottom-card-icon">DS</div>
                                    <div className="bottom-card-name">Last Year</div>
                                    <div className="bottom-card-amount">5,375</div>
                                </li>
                            </ul>
                        </Paper>
                        <Paper elevation={0} className="bottom-card">
                            <h2>Top Events</h2>
                            <ul>
                                <li>
                                    <div className="bottom-card-icon">GL</div>
                                    <div className="bottom-card-name">Glyphy</div>
                                    <div className="bottom-card-amount">264,738</div>
                                </li>
                                <li>
                                    <div className="bottom-card-icon">DS</div>
                                    <div className="bottom-card-name">DesignStripe</div>
                                    <div className="bottom-card-amount">64,738</div>
                                </li>
                                <li>
                                    <div className="bottom-card-icon">DV</div>
                                    <div className="bottom-card-name">Design Valley</div>
                                    <div className="bottom-card-amount">23,738</div>
                                </li>
                                <li>
                                    <div className="bottom-card-icon">DK</div>
                                    <div className="bottom-card-name">Neubel</div>
                                    <div className="bottom-card-amount">8,837</div>
                                </li>
                                <li>
                                    <div className="bottom-card-icon">DS</div>
                                    <div className="bottom-card-name">Last Year</div>
                                    <div className="bottom-card-amount">5,375</div>
                                </li>
                            </ul>
                        </Paper>
                        <Paper elevation={0} className="add-new-card"></Paper>
                    </div>
                </div>
            </div>
        );
    }
}

export default DashboardView;